import React from 'react';
import classNames from 'classnames';

import { TodosList } from '../TodosList'


import './User.scss'


export class User extends React.Component {



  todoStatusToggle = (todoId) => {
    // const {user} = this.props;
    const findTodo = this.props.user.todos.find( todo => todo.id === todoId)

    findTodo.completed = !findTodo.completed


    this.props.updateState()

  }


  render() {
    const { user } = this.props;
    // const {todosIsCompleted} = this.state;

    const todosStatus = user.todos.every(todo => todo.completed)



    return (
      <span className={classNames(
        "user" , 
        {'user--todos-is-completed': todosStatus}
        )}>
        <div className="user__info">
          <div className="title">
            {user.name}
          </div>
          <div className="user__about">
            <p className="email">
              <span className="label">email:</span>  {user.email}
            </p>
            <p className="phone">
              <span className="label">phone:</span>  {user.phone}
            </p>
            <p className="email">
              <span className="label">city:</span>  {user.address.city}
            </p>
          </div>
        </div>

        <div className="user__todos">

          <TodosList
            todosList={user.todos}
            onClick={this.todoStatusToggle}
          />

        </div>
      </span>
    )
  }
}



