import React from 'react';
import './App.scss';

import { UsersList } from './components/UsersList'

import users from './api/users';
import todos from './api/todos'

export class App extends React.Component {

  state = {
    nextTodoId: 1,
    chosenUser: '',
    todoDescription: '',
    usersList: users.map(user => {
      return { ...user, todos: [] }
    }),
  }

  updateState = () => {
    this.setState({})
  }

  addTodoForUser(todo) {
    const userId = this.state.usersList.findIndex(user => user.id === todo.userId);
    const user = this.state.usersList[userId];
    user.todos = [...user.todos, todo];

    this.setState(state => ({
      nextTodoId: state.nextTodoId + 1,
      chosenUser: '',
      todoDescription: '',
      usersList: [...state.usersList]
    }))

  }

  componentDidMount() {
    todos.forEach((todo) => {
      this.addTodoForUser(todo);
    })
  }


  handleSubmit = (event) => {
    event.preventDefault();
    const { chosenUser, todoDescription } = this.state;

    if (!chosenUser || !todoDescription.trim()) {
      !chosenUser ? console.log('chose name') : console.log('tape text')
      return;
    }

    const todo = {
      userId: +chosenUser,
      id: this.state.nextTodoId,
      title: todoDescription,
      completed: Math.random() > 0.5,
    }

    this.addTodoForUser(todo);
  }


  handleChange = (event) => {

    if (event.target.disabled) {
      console.log('disabled');
      this.setState({ todoDescription: '' })
      return
    }

    this.setState({ todoDescription: event.target.value })
  }

  render() {
    const { usersList } = this.state;
    const usersWithTodos = usersList.filter(user => user.todos.length)

    return (
      <div className="App" >

        <header className="header">
          <h1>Add todo</h1>
        </header>

        <main className="main">
          <form className="form" onSubmit={this.handleSubmit}>

            <span className="wraper">
              <select
                className="select"
                name="username"
                value={this.state.chosenUser || ''}
                onChange={(event) => this.setState({ chosenUser: event.target.value })}>
                <option>Chose user</option>
                {
                  users.map(user => (
                    <option key={user.id} value={user.id}>{user.name}</option>
                  ))
                }
              </select>

              <button className="button-add"> Submit </button>
            </span>

            <textarea
              name="todo-input"
              className="text-field"
              placeholder=" Todo description..."
              value={this.state.todoDescription}
              disabled={!this.state.chosenUser}
              onChange={this.handleChange}
            />

          </form>

          <section className="board">
            {
              this.state.usersList &&
              <UsersList
                usersList={usersWithTodos}
                updateState={this.updateState} 
                />
            }
          </section>
        </main>
      </div>
    )
  }
}

export default App;
