import React from 'react';
import { User } from '../User';

import classNames from 'classnames';

import './UsersList.scss'

const todoStatusToggle = (todoId) => {
  const findTodo = this.props.user.todos.find( todo => todo.id === todoId)
  findTodo.completed = !findTodo.completed
  this.setState({
    todosIsCompleted: this.props.user.todos.every(todo => todo.completed)
  })

  console.log(this.props.user.todos, this.state.todosIsCompleted);


}

export const UsersList = ({ usersList, updateState }) => (
  
  <ul className="users-list">
    {
      usersList.map(user => {
        return <li key={user.id}>
          <User user={user}
            updateState={updateState}/>
        </li>
      })
    }
  </ul>
);
