import React from 'react';
import classNames from 'classnames'

import './Todo.scss'


export const Todo = ({ todo, onClick }) => (
  <>

    <p className={classNames(
      'todo__title',
      { 'todo__title--is-completed': todo.completed }
    )}
    >
      {todo.title}
      {/* {console.log(todo)} */}
    </p>

    <span
      className={classNames(
        'todo__status',
        { 'todo__status--is-completed': todo.completed }
      )}
      onClick={() => onClick(todo.id)}
    >
      {/* {todo.completed} */}
    </span>
  </>
)